export function assertIsDefined<T> (value: T): asserts value is NonNullable<T> {
	if (value === undefined || value === null) {
		throw new Error (`Error: value${value}`);
	}
}

export function assertIsNumber (value: unknown): asserts value is number {
	if (typeof (value) !== "number") {
		throw new Error (`Error: valuenumber${typeof (value)}`);
	}
}

export function assertIsString (value: unknown): asserts value is string {
	if (typeof (value) !== "string") {
		throw new Error (`Error: valuestring${typeof (value)}`);
	}
}

export function assertIsBoolean (value: unknown): asserts value is boolean {
	if (typeof (value) !== "boolean") {
		throw new Error (`Error: valueboolean${typeof (value)}`);
	}
}
